<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/paymentMethods.png" alt="">
            <div class="curatorText">
                <img class="title_" src="../../../images/title001.png" alt="">
                <div
                v-for="(item,index) in list" @click="checked(index)"
                class="payList">
                    <div class="list-item">
                        <span class="payIcon">
                            <img :src="item.src" alt="">
                        </span>
                        <div class="left-text">
                            <h4>{{item.title}}</h4>
                            <span>{{item.text}}</span>
                            <span class="icon" :class="{checkesd : seen==index}" ></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="content">
            <p><span class="next" @click="orderPay" >确认支付</span></p>
        </div>
            <footer-text></footer-text>
    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
          list:[{
              src:'/static/img/aliPay.png',
              title:'支付宝支付',
              text:'推荐有支付宝账号的用户使用'
          },{
              src:'/static/img/wePay.png',
              title:'微信支付',
               text:'推荐安装微信5.0及以上版本'
          },{
              src:'/static/img/uPay.png',
              title:'银联支付',
               text:'推荐安装微信5.0及以上版本'
          },{
              src:'/static/img/LLpay.png',
              title:'连连支付',
              text:'推荐安装微信5.0及以上版本'
          }],
          seen:false,
          index:0
      }
  },
  methods:{
      checked:function(index){
          this.seen = index;
          this.index = index;
          //返回后台数据方法待添加
      },
      orderPay:function(){
          //支付逻辑（状态）未判断
            this.$router.push({path:'/becomeCurator/failPay'})
      }
  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 5rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        .curatorText{
            position: absolute;
            left:8%;
            width:84%;
            margin:0 auto;
            margin-top:.4rem;
            background:#fff;
            .title_{
                width:64%;
                position: absolute;
                top:-.3rem;
                left:18%;
            }
            .payList{
                border:1px solid #949494;
                box-sizing: border-box;
                .list-item{
                    text-align:left;
                    margin:.5rem auto;
                    width:80%;
                    position: relative;
                    height:1rem;
                }
                .payIcon{
                    display:inline-block;
                    img{
                        width:1rem;
                    }
                }
                .left-text{
                    position: absolute;
                    top:0.1rem;
                    left:1.2rem;
                    width:80%;
                    font-size:.35rem;
                    h4{
                        font-size:.4rem;
                        line-height: .4rem;
                    }
                    .checkesd,.icon{
                        display: inline-block;
                        width:.45rem;
                        height:.45rem;
                        position: absolute;
                        top:.275rem;
                        right:-.2rem;
                        background: url(../../../images/unselected.png) no-repeat;
                        background-size: 100%;
                    }
                    .checkesd{
                        z-index:2;
                        background: url(../../../images/selected_.png) no-repeat;
                        background-size: 100%;
                    }

                }
            }
        }
    }
    .header>img{
        width:84%;
        margin-top:14%;
    }
    .content{
        text-align: center;
        margin-top: 72%;
         .next{
             width:80%;
             height:1.4rem;
             color:#fff;
             font-size:.5rem;
             line-height: 1.4rem;
             display: inline-block;
             background-color: #EE8F45;
             border-radius: 6px;
        }

    }
</style>



