import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
          list:[{
              src:'/static/img/aliPay.png',
              title:'支付宝支付',
              text:'推荐有支付宝账号的用户使用'
          },{
              src:'/static/img/wePay.png',
              title:'微信支付',
               text:'推荐安装微信5.0及以上版本'
          },{
              src:'/static/img/uPay.png',
              title:'银联支付',
               text:'推荐安装微信5.0及以上版本'
          },{
              src:'/static/img/LLpay.png',
              title:'连连支付',
              text:'推荐安装微信5.0及以上版本'
          }],
          seen:false,
          index:0
      }
  },
  methods:{
      checked:function(index){
          this.seen = index;
          this.index = index;
          //返回后台数据方法待添加
      },
      orderPay:function(){
          //支付逻辑（状态）未判断
            this.$router.push({path:'/becomeCurator/failPay'})
      }
  }
}



